<h1 id="卡券" tabindex="-1"><a class="header-anchor" href="#卡券" aria-hidden="true">#</a> 卡券</h1>
<ul>
<li></li>
</ul>
<blockquote>
<p>Version <code>&gt;=3.1.2</code></p>
</blockquote>
<h2 id="获取实例" tabindex="-1"><a class="header-anchor" href="#获取实例" aria-hidden="true">#</a> 获取实例</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token php language-php"><span class="token delimiter important">&lt;?php</span>
<span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Foundation<span class="token punctuation">\</span>Application</span><span class="token punctuation">;</span>

<span class="token comment">// ...</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Application</span><span class="token punctuation">(</span><span class="token variable">$options</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$card</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">card</span><span class="token punctuation">;</span>
</span></code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><h2 id="api列表" tabindex="-1"><a class="header-anchor" href="#api列表" aria-hidden="true">#</a> API列表</h2>
<h3 id="获取卡券颜色" tabindex="-1"><a class="header-anchor" href="#获取卡券颜色" aria-hidden="true">#</a> 获取卡券颜色</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getColors</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getColors</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h3 id="创建卡券" tabindex="-1"><a class="header-anchor" href="#创建卡券" aria-hidden="true">#</a> 创建卡券</h3>
<p>创建卡券接口是微信卡券的基础接口，用于创建一类新的卡券，获取card_id，创建成功并通过审核后，商家可以通过文档提供的其他接口将卡券下发给用户，每次成功领取，库存数量相应扣除。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">create</span><span class="token punctuation">(</span><span class="token variable">$cardType</span><span class="token punctuation">,</span> <span class="token variable">$baseInfo</span><span class="token punctuation">,</span> <span class="token variable">$especial</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li><code>cardType</code> string - 是要添加卡券的类型</li>
<li><code>baseInfo</code> array  - 为卡券的基本数据</li>
<li><code>especial</code> array  - 是扩展字段</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token php language-php"><span class="token delimiter important">&lt;?php</span>

	<span class="token variable">$cardType</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'GROUPON'</span><span class="token punctuation">;</span>

    <span class="token variable">$baseInfo</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
        <span class="token string single-quoted-string">'logo_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://mmbiz.qpic.cn/mmbiz/2aJY6aCPatSeibYAyy7yct9zJXL9WsNVL4JdkTbBr184gNWS6nibcA75Hia9CqxicsqjYiaw2xuxYZiaibkmORS2oovdg/0'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'brand_name'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'测试商户造梦空间'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'code_type'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'CODE_TYPE_QRCODE'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'title'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'测试'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'sub_title'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'测试副标题'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'color'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'Color010'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'notice'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'测试使用时请出示此券'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'service_phone'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'15311931577'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'description'</span> <span class="token operator">=></span> <span class="token string double-quoted-string">"测试不可与其他优惠同享\n如需团购券发票，请在消费时向商户提出\n店内均可使用，仅限堂食"</span><span class="token punctuation">,</span>

        <span class="token string single-quoted-string">'date_info'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
          <span class="token string single-quoted-string">'type'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'DATE_TYPE_FIX_TERM'</span><span class="token punctuation">,</span>
          <span class="token string single-quoted-string">'fixed_term'</span> <span class="token operator">=></span> <span class="token number">90</span><span class="token punctuation">,</span> <span class="token comment">//表示自领取后多少天内有效，不支持填写0</span>
          <span class="token string single-quoted-string">'fixed_begin_term'</span> <span class="token operator">=></span> <span class="token number">0</span><span class="token punctuation">,</span> <span class="token comment">//表示自领取后多少天开始生效，领取后当天生效填写0。</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>

        <span class="token string single-quoted-string">'sku'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
          <span class="token string single-quoted-string">'quantity'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'0'</span><span class="token punctuation">,</span> <span class="token comment">//自定义code时设置库存为0</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>

        <span class="token string single-quoted-string">'location_id_list'</span> <span class="token operator">=></span> <span class="token punctuation">[</span><span class="token string single-quoted-string">'461907340'</span><span class="token punctuation">]</span><span class="token punctuation">,</span>  <span class="token comment">//获取门店位置poi_id，具备线下门店的商户为必填</span>

        <span class="token string single-quoted-string">'get_limit'</span> <span class="token operator">=></span> <span class="token number">1</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'use_custom_code'</span> <span class="token operator">=></span> <span class="token constant boolean">true</span><span class="token punctuation">,</span> <span class="token comment">//自定义code时必须为true</span>
        <span class="token string single-quoted-string">'get_custom_code_mode'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'GET_CUSTOM_CODE_MODE_DEPOSIT'</span><span class="token punctuation">,</span>  <span class="token comment">//自定义code时设置</span>
        <span class="token string single-quoted-string">'bind_openid'</span> <span class="token operator">=></span> <span class="token constant boolean">false</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'can_share'</span> <span class="token operator">=></span> <span class="token constant boolean">true</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'can_give_friend'</span> <span class="token operator">=></span> <span class="token constant boolean">false</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'center_title'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'顶部居中按钮'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'center_sub_title'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'按钮下方的wording'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'center_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://www.qq.com'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'custom_url_name'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'立即使用'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'custom_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://www.qq.com'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'custom_url_sub_title'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'6个汉字tips'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'promotion_url_name'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'更多优惠'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'promotion_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://www.qq.com'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'source'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'造梦空间'</span><span class="token punctuation">,</span>
      <span class="token punctuation">]</span><span class="token punctuation">;</span>

    <span class="token variable">$especial</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
      <span class="token string single-quoted-string">'deal_detail'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'deal_detail'</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">;</span>

    <span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">create</span><span class="token punctuation">(</span><span class="token variable">$cardType</span><span class="token punctuation">,</span> <span class="token variable">$baseInfo</span><span class="token punctuation">,</span> <span class="token variable">$especial</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</span></code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br><span class="line-number">30</span><br><span class="line-number">31</span><br><span class="line-number">32</span><br><span class="line-number">33</span><br><span class="line-number">34</span><br><span class="line-number">35</span><br><span class="line-number">36</span><br><span class="line-number">37</span><br><span class="line-number">38</span><br><span class="line-number">39</span><br><span class="line-number">40</span><br><span class="line-number">41</span><br><span class="line-number">42</span><br><span class="line-number">43</span><br><span class="line-number">44</span><br><span class="line-number">45</span><br><span class="line-number">46</span><br><span class="line-number">47</span><br><span class="line-number">48</span><br><span class="line-number">49</span><br></div></div><h3 id="创建二维码" tabindex="-1"><a class="header-anchor" href="#创建二维码" aria-hidden="true">#</a> 创建二维码</h3>
<p>开发者可调用该接口生成一张卡券二维码供用户扫码后添加卡券到卡包。</p>
<p>自定义Code码的卡券调用接口时，POST数据中需指定code，非自定义code不需指定，指定openid同理。指定后的二维码只能被用户扫描领取一次。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">QRCode</span><span class="token punctuation">(</span><span class="token variable">$cards</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li><code>cards</code> array - 卡券相关信息</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token comment">//领取单张卡券</span>
<span class="token variable">$cards</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'action_name'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'QR_CARD'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'expire_seconds'</span> <span class="token operator">=></span> <span class="token number">1800</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'action_info'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
      <span class="token string single-quoted-string">'card'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
        <span class="token string single-quoted-string">'card_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pdkJ9uFS2WWCFfbbEfsAzrzizVyY'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'is_unique_code'</span> <span class="token operator">=></span> <span class="token constant boolean">false</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'outer_id'</span> <span class="token operator">=></span> <span class="token number">1</span><span class="token punctuation">,</span>
      <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">,</span>
  <span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">QRCode</span><span class="token punctuation">(</span><span class="token variable">$cards</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br></div></div><div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token comment">//领取多张卡券</span>
<span class="token variable">$cards</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'action_name'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'QR_MULTIPLE_CARD'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'action_info'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
      <span class="token string single-quoted-string">'multiple_card'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
        <span class="token string single-quoted-string">'card_list'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
          <span class="token punctuation">[</span><span class="token string single-quoted-string">'card_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pdkJ9uFS2WWCFfbbEfsAzrzizVyY'</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>
      <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">,</span>
  <span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">QRCode</span><span class="token punctuation">(</span><span class="token variable">$cardList</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div><p>请求成功返回值示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword">array</span><span class="token punctuation">(</span><span class="token number">4</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
  <span class="token punctuation">[</span><span class="token string double-quoted-string">"ticket"</span><span class="token punctuation">]</span><span class="token operator">=></span>
  <span class="token keyword type-declaration">string</span><span class="token punctuation">(</span><span class="token number">96</span><span class="token punctuation">)</span> <span class="token string double-quoted-string">"gQHa7joAAAAAAAAAASxodHRwOi8vd2VpeGluLnFxLmNvbS9xLzdrUFlQMHJsV3Zvanc5a2NzV1N5AAIEJUVyVwMEAKd2AA=="</span>
  <span class="token punctuation">[</span><span class="token string double-quoted-string">"expire_seconds"</span><span class="token punctuation">]</span><span class="token operator">=></span>
  <span class="token keyword type-declaration">int</span><span class="token punctuation">(</span><span class="token number">7776000</span><span class="token punctuation">)</span>
  <span class="token punctuation">[</span><span class="token string double-quoted-string">"url"</span><span class="token punctuation">]</span><span class="token operator">=></span>
  <span class="token keyword type-declaration">string</span><span class="token punctuation">(</span><span class="token number">43</span><span class="token punctuation">)</span> <span class="token string double-quoted-string">"http://weixin.qq.com/q/7kPYP0rlWvojw9kcsWSy"</span>
  <span class="token punctuation">[</span><span class="token string double-quoted-string">"show_qrcode_url"</span><span class="token punctuation">]</span><span class="token operator">=></span>
  <span class="token keyword type-declaration">string</span><span class="token punctuation">(</span><span class="token number">151</span><span class="token punctuation">)</span> <span class="token string double-quoted-string">"https://mp.weixin.qq.com/cgi-bin/showqrcode?ticket=gQHa7joAAAAAAAAAASxodHRwOi8vd2VpeGluLnFxLmNvbS9xLzdrUFlQMHJsV3Zvanc5a2NzV1N5AAIEJUVyVwMEAKd2AA%3D%3D"</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br></div></div><p>成功返回值列表说明：</p>
<table>
<thead>
<tr>
<th style="text-align:center">参数名</th>
<th style="text-align:left">描述</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:center">ticket</td>
<td style="text-align:left">获取的二维码ticket，凭借此ticket调用<a href="http://mp.weixin.qq.com/wiki?t=resource/res_main&amp;id=mp1443433542&amp;token=&amp;lang=zh_CN" target="_blank" rel="noopener noreferrer">通过ticket换取二维码接口<OutboundLink/></a>可以在有效时间内换取二维码。</td>
</tr>
<tr>
<td style="text-align:center">expire_seconds</td>
<td style="text-align:left">二维码的有效时间</td>
</tr>
<tr>
<td style="text-align:center">url</td>
<td style="text-align:left">二维码图片解析后的地址，开发者可根据该地址自行生成需要的二维码图片</td>
</tr>
<tr>
<td style="text-align:center">show_qrcode_url</td>
<td style="text-align:left">二维码显示地址，点击后跳转二维码页面</td>
</tr>
</tbody>
</table>
<h3 id="ticket-换取二维码图片" tabindex="-1"><a class="header-anchor" href="#ticket-换取二维码图片" aria-hidden="true">#</a> ticket 换取二维码图片</h3>
<p>获取二维码 ticket 后，开发者可用 ticket 换取二维码图片。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">showQRCode</span><span class="token punctuation">(</span><span class="token variable">$ticket</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li><code>ticket</code> string  - 获取的二维码 ticket，凭借此 ticket 可以在有效时间内换取二维码。</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$ticket</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'gQFF8DoAAAAAAAAAASxodHRwOi8vd2VpeGluLnFxLmNvbS9xL01VTzN0T0hsS1BwUlBBYUszbVN5AAIEughxVwMEAKd2AA=='</span><span class="token punctuation">;</span>
<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">showQRCode</span><span class="token punctuation">(</span><span class="token variable">$ticket</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h3 id="ticket-换取二维码链接" tabindex="-1"><a class="header-anchor" href="#ticket-换取二维码链接" aria-hidden="true">#</a> ticket 换取二维码链接</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getQRCodeUrl</span><span class="token punctuation">(</span><span class="token variable">$ticket</span><span class="token punctuation">)</span><span class="token punctuation">;</span>  <span class="token comment">//获取的二维码ticket</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$ticket</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'gQFF8DoAAAAAAAAAASxodHRwOi8vd2VpeGluLnFxLmNvbS9xL01VTzN0T0hsS1BwUlBBYUszbVN5AAIEughxVwMEAKd2AA=='</span><span class="token punctuation">;</span>
<span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getQRCodeUrl</span><span class="token punctuation">(</span><span class="token variable">$ticket</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><h3 id="jsapi-卡券批量下发到用户" tabindex="-1"><a class="header-anchor" href="#jsapi-卡券批量下发到用户" aria-hidden="true">#</a> JSAPI 卡券批量下发到用户</h3>
<p>微信卡券：JSAPI 卡券</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cards</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token punctuation">[</span><span class="token string single-quoted-string">'card_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pdkJ9uLRSbnB3UFEjZAgUxAJrjeY'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'outer_id'</span> <span class="token operator">=></span> <span class="token number">2</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">[</span><span class="token string single-quoted-string">'card_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pdkJ9uJ37aU-tyRj4_grs8S45k1c'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'outer_id'</span> <span class="token operator">=></span> <span class="token number">3</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$json</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">jsConfigForAssign</span><span class="token punctuation">(</span><span class="token variable">$cards</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 返回 json 格式</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><p>返回 json，在模板里的用法：</p>
<div class="language-html ext-html line-numbers-mode"><pre v-pre class="language-html"><code>wx.addCard({
    cardList: <span class="token prolog">&lt;?= $json ?></span>, // 需要打开的卡券列表
    success: function (res) {
        var cardList = res.cardList; // 添加的卡券列表信息
    }
});
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h3 id="创建货架接口" tabindex="-1"><a class="header-anchor" href="#创建货架接口" aria-hidden="true">#</a> 创建货架接口</h3>
<p>开发者需调用该接口创建货架链接，用于卡券投放。创建货架时需填写投放路径的场景字段。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">createLandingPage</span><span class="token punctuation">(</span><span class="token variable">$banner</span><span class="token punctuation">,</span> <span class="token variable">$pageTitle</span><span class="token punctuation">,</span> <span class="token variable">$canShare</span><span class="token punctuation">,</span> <span class="token variable">$scene</span><span class="token punctuation">,</span> <span class="token variable">$cards</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li><code>banner</code> string -页面的 banner 图;</li>
<li><code>pageTitle</code> string - 页面的 title</li>
<li><code>canShare</code> bool - 页面是不是可以分享，true 或 false</li>
<li><code>scene</code>  string - 投放页面的场景值，具体值请参考下面的 example</li>
<li><code>cards</code>  array - 卡券列表，每个元素有两个字段</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$banner</span>     <span class="token operator">=</span> <span class="token string single-quoted-string">'http://mmbiz.qpic.cn/mmbiz/iaL1LJM1mF9aRKPZJkmG8xXhiaHqkKSVMMWeN3hLut7X7hicFN'</span><span class="token punctuation">;</span>
<span class="token variable">$pageTitle</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'惠城优惠大派送'</span><span class="token punctuation">;</span>
<span class="token variable">$canShare</span>  <span class="token operator">=</span> <span class="token constant boolean">true</span><span class="token punctuation">;</span>

<span class="token comment">//SCENE_NEAR_BY          附近</span>
<span class="token comment">//SCENE_MENU             自定义菜单</span>
<span class="token comment">//SCENE_QRCODE             二维码</span>
<span class="token comment">//SCENE_ARTICLE             公众号文章</span>
<span class="token comment">//SCENE_H5                 h5页面</span>
<span class="token comment">//SCENE_IVR                 自动回复</span>
<span class="token comment">//SCENE_CARD_CUSTOM_CELL 卡券自定义cell</span>
<span class="token variable">$scene</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'SCENE_NEAR_BY'</span><span class="token punctuation">;</span>

<span class="token variable">$cardList</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token punctuation">[</span><span class="token string single-quoted-string">'card_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pdkJ9uLRSbnB3UFEjZAgUxAJrjeY'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'thumb_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://test.digilinx.cn/wxApi/Uploads/test.png'</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">[</span><span class="token string single-quoted-string">'card_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pdkJ9uJ37aU-tyRj4_grs8S45k1c'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'thumb_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://test.digilinx.cn/wxApi/Uploads/aa.jpg'</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">createLandingPage</span><span class="token punctuation">(</span><span class="token variable">$banner</span><span class="token punctuation">,</span> <span class="token variable">$pageTitle</span><span class="token punctuation">,</span> <span class="token variable">$canShare</span><span class="token punctuation">,</span> <span class="token variable">$scene</span><span class="token punctuation">,</span> <span class="token variable">$cardList</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br></div></div><h3 id="导入code接口" tabindex="-1"><a class="header-anchor" href="#导入code接口" aria-hidden="true">#</a> 导入code接口</h3>
<p>在自定义code卡券成功创建并且通过审核后，必须将自定义code按照与发券方的约定数量调用导入code接口导入微信后台。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">deposit</span><span class="token punctuation">(</span><span class="token variable">$card_id</span><span class="token punctuation">,</span> <span class="token variable">$code</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li><code>cardId</code> string - 要导入code的卡券ID</li>
<li><code>code</code> string - 要导入微信卡券后台的自定义 code，最多100个</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uLCEF_HSKO7JdQOUcZ-PUzo'</span><span class="token punctuation">;</span>
<span class="token variable">$code</span>    <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token string single-quoted-string">'11111'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'22222'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'33333'</span><span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">deposit</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$code</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h3 id="查询导入code数目" tabindex="-1"><a class="header-anchor" href="#查询导入code数目" aria-hidden="true">#</a> 查询导入code数目</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getDepositedCount</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>  <span class="token comment">//要导入code的卡券ID</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uLCEF_HSKO7JdQOUcZ-PUzo'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getDepositedCount</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="核查code接口" tabindex="-1"><a class="header-anchor" href="#核查code接口" aria-hidden="true">#</a> 核查code接口</h3>
<p>为了避免出现导入差错，强烈建议开发者在查询完code数目的时候核查code接口校验code导入微信后台的情况。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">checkCode</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$code</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uLCEF_HSKO7JdQOUcZ-PUzo'</span><span class="token punctuation">;</span>

<span class="token variable">$code</span> <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token string single-quoted-string">'807732265476'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'22222'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'33333'</span><span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">checkCode</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$code</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h3 id="图文消息群发卡券" tabindex="-1"><a class="header-anchor" href="#图文消息群发卡券" aria-hidden="true">#</a> 图文消息群发卡券</h3>
<p>特别注意：目前该接口仅支持填入非自定义code的卡券,自定义code的卡券需先进行code导入后调用。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getHtml</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uLCEF_HSKO7JdQOUcZ-PUzo'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getHtml</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="设置测试白名单" tabindex="-1"><a class="header-anchor" href="#设置测试白名单" aria-hidden="true">#</a> 设置测试白名单</h3>
<p>同时支持“openid”、“username”两种字段设置白名单，总数上限为10个。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">setTestWhitelist</span><span class="token punctuation">(</span><span class="token variable">$openids</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 使用 openid</span>
<span class="token variable">$card</span><span class="token operator">-></span><span class="token function">setTestWhitelistByUsername</span><span class="token punctuation">(</span><span class="token variable">$usernames</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 使用 username</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><ul>
<li><code>openids</code> array - 测试的openid列表</li>
<li><code>usernames</code> array  - 测试的微信号列表</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token comment">// by openid</span>
<span class="token variable">$openids</span>   <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token variable">$openId</span><span class="token punctuation">,</span> <span class="token variable">$openId2</span><span class="token punctuation">,</span> <span class="token variable">$openid3</span><span class="token operator">...</span><span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">setTestWhitelist</span><span class="token punctuation">(</span><span class="token variable">$openids</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// by username</span>
<span class="token variable">$usernames</span> <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token string single-quoted-string">'tianye0327'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'iovertrue'</span><span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">setTestWhitelistByUsername</span><span class="token punctuation">(</span><span class="token variable">$usernames</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><h3 id="查询code接口" tabindex="-1"><a class="header-anchor" href="#查询code接口" aria-hidden="true">#</a> 查询Code接口</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getCode</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$checkConsume</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li>checkConsume  是否校验code核销状态，true和false</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$code</span>          <span class="token operator">=</span> <span class="token string single-quoted-string">'736052543512'</span><span class="token punctuation">;</span>
<span class="token variable">$checkConsume</span> <span class="token operator">=</span> <span class="token constant boolean">true</span><span class="token punctuation">;</span>
<span class="token variable">$cardId</span>       <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uDgnm0pKfrTb1yV0dFMO_Gk'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getCode</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$checkConsume</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h3 id="核销code接口" tabindex="-1"><a class="header-anchor" href="#核销code接口" aria-hidden="true">#</a> 核销Code接口</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">consume</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 或者指定 cardId</span>

<span class="token variable">$card</span><span class="token operator">-></span><span class="token function">consume</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uDmhkLj6l5bm3cq9iteQBck'</span><span class="token punctuation">;</span>
<span class="token variable">$code</span>    <span class="token operator">=</span> <span class="token string single-quoted-string">'789248558333'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">consume</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">//或</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">consume</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><h3 id="code解码接口" tabindex="-1"><a class="header-anchor" href="#code解码接口" aria-hidden="true">#</a> Code解码接口</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">decryptCode</span><span class="token punctuation">(</span><span class="token variable">$encryptedCode</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$encryptedCode</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'XXIzTtMqCxwOaawoE91+VJdsFmv7b8g0VZIZkqf4GWA60Fzpc8ksZ/5ZZ0DVkXdE'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">decryptCode</span><span class="token punctuation">(</span><span class="token variable">$encryptedCode</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="获取用户已领取卡券接口" tabindex="-1"><a class="header-anchor" href="#获取用户已领取卡券接口" aria-hidden="true">#</a> 获取用户已领取卡券接口</h3>
<p>用于获取用户卡包里的，属于该appid下所有<strong>可用卡券，包括正常状态和未生效状态</strong>。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getUserCards</span><span class="token punctuation">(</span><span class="token variable">$openid</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$openid</span>  <span class="token operator">=</span> <span class="token string single-quoted-string">'odkJ9uDUz26RY-7DN1mxkznfo9xU'</span><span class="token punctuation">;</span>
<span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">;</span> <span class="token comment">//卡券ID。不填写时默认查询当前appid下的卡券。</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getUserCards</span><span class="token punctuation">(</span><span class="token variable">$openid</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h3 id="查看卡券详情" tabindex="-1"><a class="header-anchor" href="#查看卡券详情" aria-hidden="true">#</a> 查看卡券详情</h3>
<p>开发者可以调用该接口查询某个card_id的创建信息、审核状态以及库存数量。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getCard</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uLRSbnB3UFEjZAgUxAJrjeY'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getCard</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="批量查询卡列表" tabindex="-1"><a class="header-anchor" href="#批量查询卡列表" aria-hidden="true">#</a> 批量查询卡列表</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">lists</span><span class="token punctuation">(</span><span class="token variable">$offset</span><span class="token punctuation">,</span> <span class="token variable">$count</span><span class="token punctuation">,</span> <span class="token variable">$statusList</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li><code>offset</code> int - 查询卡列表的起始偏移量，从0开始</li>
<li><code>count</code> int - 需要查询的卡片的数量</li>
<li><code>statusList</code> -  支持开发者拉出指定状态的卡券列表，详见example</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$offset</span>      <span class="token operator">=</span> <span class="token number">0</span><span class="token punctuation">;</span>
<span class="token variable">$count</span>       <span class="token operator">=</span> <span class="token number">10</span><span class="token punctuation">;</span>

<span class="token comment">//CARD_STATUS_NOT_VERIFY,待审核；</span>
<span class="token comment">//CARD_STATUS_VERIFY_FAIL,审核失败；</span>
<span class="token comment">//CARD_STATUS_VERIFY_OK，通过审核；</span>
<span class="token comment">//CARD_STATUS_USER_DELETE，卡券被商户删除；</span>
<span class="token comment">//CARD_STATUS_DISPATCH，在公众平台投放过的卡券；</span>
<span class="token variable">$statusList</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'CARD_STATUS_VERIFY_OK'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">lists</span><span class="token punctuation">(</span><span class="token variable">$offset</span><span class="token punctuation">,</span> <span class="token variable">$count</span><span class="token punctuation">,</span> <span class="token variable">$statusList</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div><h3 id="更改卡券信息接口" tabindex="-1"><a class="header-anchor" href="#更改卡券信息接口" aria-hidden="true">#</a> 更改卡券信息接口</h3>
<p>支持更新所有卡券类型的部分通用字段及特殊卡券中特定字段的信息。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">update</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$type</span><span class="token punctuation">,</span> <span class="token variable">$baseInfo</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li><code>type</code> string - 卡券类型</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uCzKWebwgNjxosee0ZuO3Os'</span><span class="token punctuation">;</span>

<span class="token variable">$type</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'groupon'</span><span class="token punctuation">;</span>

<span class="token variable">$baseInfo</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'logo_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://mmbiz.qpic.cn/mmbiz/2aJY6aCPatSeibYAyy7yct9zJXL9WsNVL4JdkTbBr184gNWS6nibcA75Hia9CqxicsqjYiaw2xuxYZiaibkmORS2oovdg/0'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'center_title'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'顶部居中按钮'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'center_sub_title'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'按钮下方的wording'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'center_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://www.baidu.com'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'custom_url_name'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'立即使用'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'custom_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://www.qq.com'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'custom_url_sub_title'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'6个汉字tips'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'promotion_url_name'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'更多优惠'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'promotion_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://www.qq.com'</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">update</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$type</span><span class="token punctuation">,</span> <span class="token variable">$baseInfo</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br></div></div><h3 id="设置微信买单接口" tabindex="-1"><a class="header-anchor" href="#设置微信买单接口" aria-hidden="true">#</a> 设置微信买单接口</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">setPayCell</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$isOpen</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li><code>isOpen</code> string - 是否开启买单功能，填 true/false，不填默认 true</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uH7u11R-Tu1kilbaW_zDFow'</span><span class="token punctuation">;</span>
<span class="token variable">$isOpen</span> <span class="token operator">=</span> <span class="token constant boolean">true</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">setPayCell</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$isOpen</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h3 id="修改库存接口" tabindex="-1"><a class="header-anchor" href="#修改库存接口" aria-hidden="true">#</a> 修改库存接口</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">increaseStock</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$amount</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 增加库存</span>
<span class="token variable">$card</span><span class="token operator">-></span><span class="token function">reductStock</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$amount</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 减少库存</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><ul>
<li><code>cardId</code> string - 卡券 ID</li>
<li><code>amount</code> int - 修改多少库存</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uLRSbnB3UFEjZAgUxAJrjeY'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">increaseStock</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token number">100</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="更改code接口" tabindex="-1"><a class="header-anchor" href="#更改code接口" aria-hidden="true">#</a> 更改Code接口</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">updateCode</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$newCode</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li><code>newCode</code> string - 变更后的有效Code码</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$code</span>     <span class="token operator">=</span> <span class="token string single-quoted-string">'148246271394'</span><span class="token punctuation">;</span>
<span class="token variable">$newCode</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'659266965266'</span><span class="token punctuation">;</span>
<span class="token variable">$cardId</span>  <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">updateCode</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$newCode</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h3 id="删除卡券接口" tabindex="-1"><a class="header-anchor" href="#删除卡券接口" aria-hidden="true">#</a> 删除卡券接口</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">delete</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uItT7iUpBp4GjZp8Cae0Vig'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">delete</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="设置卡券失效" tabindex="-1"><a class="header-anchor" href="#设置卡券失效" aria-hidden="true">#</a> 设置卡券失效</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">disable</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$code</span>    <span class="token operator">=</span> <span class="token string single-quoted-string">'736052543512'</span><span class="token punctuation">;</span>
<span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">disable</span><span class="token punctuation">(</span><span class="token variable">$code</span><span class="token punctuation">,</span> <span class="token variable">$cardId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h3 id="会员卡接口激活" tabindex="-1"><a class="header-anchor" href="#会员卡接口激活" aria-hidden="true">#</a> 会员卡接口激活</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">activate</span><span class="token punctuation">(</span><span class="token variable">$info</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li><code>info</code> - 需要激活的会员卡信息</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$activate</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
      <span class="token string single-quoted-string">'membership_number'</span>        <span class="token operator">=></span> <span class="token string single-quoted-string">'357898858'</span><span class="token punctuation">,</span> <span class="token comment">//会员卡编号，由开发者填入，作为序列号显示在用户的卡包里。可与Code码保持等值。</span>
      <span class="token string single-quoted-string">'code'</span>                     <span class="token operator">=></span> <span class="token string single-quoted-string">'916679873278'</span><span class="token punctuation">,</span> <span class="token comment">//创建会员卡时获取的初始code。</span>
      <span class="token string single-quoted-string">'activate_begin_time'</span>      <span class="token operator">=></span> <span class="token string single-quoted-string">'1397577600'</span><span class="token punctuation">,</span> <span class="token comment">//激活后的有效起始时间。若不填写默认以创建时的 data_info 为准。Unix时间戳格式</span>
      <span class="token string single-quoted-string">'activate_end_time'</span>        <span class="token operator">=></span> <span class="token string single-quoted-string">'1422724261'</span><span class="token punctuation">,</span> <span class="token comment">//激活后的有效截至时间。若不填写默认以创建时的 data_info 为准。Unix时间戳格式。</span>
      <span class="token string single-quoted-string">'init_bonus'</span>               <span class="token operator">=></span> <span class="token string single-quoted-string">'持白金会员卡到店消费，可享8折优惠。'</span><span class="token punctuation">,</span> <span class="token comment">//初始积分，不填为0。</span>
      <span class="token string single-quoted-string">'init_balance'</span>             <span class="token operator">=></span> <span class="token string single-quoted-string">'持白金会员卡到店消费，可享8折优惠。'</span><span class="token punctuation">,</span> <span class="token comment">//初始余额，不填为0。</span>
      <span class="token string single-quoted-string">'init_custom_field_value1'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'白银'</span><span class="token punctuation">,</span> <span class="token comment">//创建时字段custom_field1定义类型的初始值，限制为4个汉字，12字节。</span>
      <span class="token string single-quoted-string">'init_custom_field_value2'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'9折'</span><span class="token punctuation">,</span> <span class="token comment">//创建时字段custom_field2定义类型的初始值，限制为4个汉字，12字节。</span>
      <span class="token string single-quoted-string">'init_custom_field_value3'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'200'</span><span class="token punctuation">,</span> <span class="token comment">//创建时字段custom_field3定义类型的初始值，限制为4个汉字，12字节。</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">activate</span><span class="token punctuation">(</span><span class="token variable">$activate</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div><h3 id="设置开卡字段接口" tabindex="-1"><a class="header-anchor" href="#设置开卡字段接口" aria-hidden="true">#</a> 设置开卡字段接口</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">activateUserForm</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$requiredForm</span><span class="token punctuation">,</span> <span class="token variable">$optionalForm</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li><code>requiredForm</code> array - 会员卡激活时的必填选项</li>
<li><code>optionalForm</code> array - 会员卡激活时的选填项</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pdkJ9uJYAyfLXsUCwI2LdH2Pn1AU'</span><span class="token punctuation">;</span>

<span class="token variable">$requiredForm</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'required_form'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
        <span class="token string single-quoted-string">'common_field_id_list'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
            <span class="token string single-quoted-string">'USER_FORM_INFO_FLAG_MOBILE'</span><span class="token punctuation">,</span>
            <span class="token string single-quoted-string">'USER_FORM_INFO_FLAG_LOCATION'</span><span class="token punctuation">,</span>
            <span class="token string single-quoted-string">'USER_FORM_INFO_FLAG_BIRTHDAY'</span><span class="token punctuation">,</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'custom_field_list'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
            <span class="token string single-quoted-string">'喜欢的食物'</span><span class="token punctuation">,</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$optionalForm</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'optional_form'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
        <span class="token string single-quoted-string">'common_field_id_list'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
            <span class="token string single-quoted-string">'USER_FORM_INFO_FLAG_EMAIL'</span><span class="token punctuation">,</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'custom_field_list'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
            <span class="token string single-quoted-string">'喜欢的食物'</span><span class="token punctuation">,</span>
        <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">activateUserForm</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$requiredForm</span><span class="token punctuation">,</span> <span class="token variable">$optionalForm</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br></div></div><h3 id="拉取会员信息接口" tabindex="-1"><a class="header-anchor" href="#拉取会员信息接口" aria-hidden="true">#</a> 拉取会员信息接口</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getMemberCardUser</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$code</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$cardId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'pbLatjtZ7v1BG_ZnTjbW85GYc_E8'</span><span class="token punctuation">;</span>
<span class="token variable">$code</span>    <span class="token operator">=</span> <span class="token string single-quoted-string">'916679873278'</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">getMemberCardUser</span><span class="token punctuation">(</span><span class="token variable">$cardId</span><span class="token punctuation">,</span> <span class="token variable">$code</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h3 id="更新会员信息" tabindex="-1"><a class="header-anchor" href="#更新会员信息" aria-hidden="true">#</a> 更新会员信息</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">updateMemberCardUser</span><span class="token punctuation">(</span><span class="token variable">$updateUser</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li><code>updateUser</code> array - 可以更新的会员信息</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$updateUser</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'code'</span>                <span class="token operator">=></span> <span class="token string single-quoted-string">'916679873278'</span><span class="token punctuation">,</span> <span class="token comment">//卡券Code码。</span>
    <span class="token string single-quoted-string">'card_id'</span>             <span class="token operator">=></span> <span class="token string single-quoted-string">'pbLatjtZ7v1BG_ZnTjbW85GYc_E8'</span><span class="token punctuation">,</span> <span class="token comment">//卡券ID。</span>
    <span class="token string single-quoted-string">'record_bonus'</span>        <span class="token operator">=></span> <span class="token string single-quoted-string">'消费30元，获得3积分'</span><span class="token punctuation">,</span> <span class="token comment">//商家自定义积分消耗记录，不超过14个汉字。</span>
    <span class="token string single-quoted-string">'bonus'</span>               <span class="token operator">=></span> <span class="token string single-quoted-string">'100'</span><span class="token punctuation">,</span> <span class="token comment">//需要设置的积分全量值，传入的数值会直接显示，如果同时传入add_bonus和bonus,则前者无效。</span>
    <span class="token string single-quoted-string">'balance'</span>             <span class="token operator">=></span> <span class="token string single-quoted-string">'持白金会员卡到店消费，可享8折优惠。'</span><span class="token punctuation">,</span> <span class="token comment">//需要设置的余额全量值，传入的数值会直接显示，如果同时传入add_balance和balance,则前者无效。</span>
    <span class="token string single-quoted-string">'record_balance'</span>      <span class="token operator">=></span> <span class="token string single-quoted-string">'持白金会员卡到店消费，可享8折优惠。'</span><span class="token punctuation">,</span> <span class="token comment">//商家自定义金额消耗记录，不超过14个汉字。</span>
    <span class="token string single-quoted-string">'custom_field_value1'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'100'</span><span class="token punctuation">,</span> <span class="token comment">//创建时字段custom_field1定义类型的最新数值，限制为4个汉字，12字节。</span>
    <span class="token string single-quoted-string">'custom_field_value2'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'200'</span><span class="token punctuation">,</span> <span class="token comment">//创建时字段custom_field2定义类型的最新数值，限制为4个汉字，12字节。</span>
    <span class="token string single-quoted-string">'custom_field_value3'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'300'</span><span class="token punctuation">,</span> <span class="token comment">//创建时字段custom_field3定义类型的最新数值，限制为4个汉字，12字节。</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">updateMemberCardUser</span><span class="token punctuation">(</span><span class="token variable">$updateUser</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div><h3 id="添加子商户" tabindex="-1"><a class="header-anchor" href="#添加子商户" aria-hidden="true">#</a> 添加子商户</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">craeteSubMerchant</span><span class="token punctuation">(</span><span class="token variable">$brandName</span><span class="token punctuation">,</span> <span class="token variable">$logoUrl</span><span class="token punctuation">,</span> <span class="token variable">$protocol</span><span class="token punctuation">,</span> <span class="token variable">$endTime</span><span class="token punctuation">,</span> <span class="token variable">$primaryCategoryId</span><span class="token punctuation">,</span> <span class="token variable">$secondaryCategoryId</span><span class="token punctuation">,</span> <span class="token variable">$agreementMediaId</span><span class="token punctuation">,</span> <span class="token variable">$operatorMediaId</span><span class="token punctuation">,</span> <span class="token variable">$appId</span><span class="token punctuation">)</span><span class="token punctuation">;</span> 
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li><code>brand_name</code> string - 子商户名称（12个汉字内），该名称将在制券时填入并显示在卡券页面上</li>
<li><code>logo_url</code>  string - 子商户 logo，可通过上传 logo 接口获取。该 logo 将在制券时填入并显示在卡券页面上</li>
<li><code>protocol</code>  string - 授权函ID，即通过上传临时素材接口上传授权函后获得的 meida_id</li>
<li><code>primary_category_id</code>  int - 一级类目id,可以通过本文档中接口查询</li>
<li><code>secondary_category_id</code> int - 二级类目id，可以通过本文档中接口查询</li>
<li><code>agreement_media_id</code>  string - 营业执照或个体工商户营业执照彩照或扫描件</li>
<li><code>operator_media_id</code>  string - 营业执照内登记的经营者身份证彩照或扫描件</li>
<li><code>app_id</code>  string - 子商户的公众号 app_id，配置后子商户卡券券面上的 app_id 为该 app_id, app_id 须经过认证</li>
</ul>
<p>example:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$info</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'brand_name'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'overtrue'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'logo_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://mmbiz.qpic.cn/mmbiz/iaL1LJM1mF9aRKPZJkmG8xXhiaHqkKSVMMWeN3hLut7X7hicFNjakmxibMLGWpXrEXB33367o7zHN0CwngnQY7zb7g/0'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'protocol'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'qIqwTfzAdJ_1-VJFT0fIV53DSY4sZY2WyhkzZzbV498Qgdp-K5HJtZihbHLS0Ys0'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'end_time'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'1438990559'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'primary_category_id'</span> <span class="token operator">=></span> <span class="token number">1</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'secondary_category_id'</span> <span class="token operator">=></span> <span class="token number">101</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'agreement_media_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">''</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'operator_media_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">''</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'app_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">''</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$card</span><span class="token operator">-></span><span class="token function">createSubMerchant</span><span class="token punctuation">(</span><span class="token variable">$info</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div><h3 id="更新子商户" tabindex="-1"><a class="header-anchor" href="#更新子商户" aria-hidden="true">#</a> 更新子商户</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$card</span><span class="token operator">-></span><span class="token function">updateSubMerchant</span><span class="token punctuation">(</span><span class="token variable">$merchantId</span><span class="token punctuation">,</span> <span class="token variable">$info</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li><code>$merchantId</code> int - 子商户 ID</li>
<li><code>$info</code> array - 参数与创建子商户参数一样</li>
</ul>
<p>example:</p>
