</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><h3 id="卡券开放类目查询接口" tabindex="-1"><a class="header-anchor" href="#卡券开放类目查询接口" aria-hidden="true">#</a> 卡券开放类目查询接口</h3>
